









































































import { Component, Vue } from "vue-property-decorator";
import Services from "./Services.vue";
import {
  getAuth,
  onAuthStateChanged,
  User,
  Auth,
  signOut,
  deleteUser,
} from "firebase/auth";
import {
  collection,
  Firestore,
  getDocs,
  doc,
  QueryDocumentSnapshot,
  QuerySnapshot,
  getDoc,
  DocumentSnapshot,
  query,
  where,
  orderBy,
  limit,
  onSnapshot,
  deleteDoc,
} from "firebase/firestore";
import { firebaseConfig } from "@/myconfig";
import { initializeApp, FirebaseApp } from "firebase/app";
import { getFirestore, setDoc } from "firebase/firestore";
import Chartkick from "vue-chartkick";
import Chart from "chart.js";

type Client = {
  name: string;
  id: string;
  phoneNumber: string;
  date: any;
  time: any;
  selectedServices: string;
};

@Component
export default class Scheduling extends Vue {
  clientName = "";
  clientNumber = "";
  clientID = "";
  selectedDate: any = null;
  selectedTime: any = null;
  selectedServices = "";
  selectedClient: Array<Client> = [];
  selectedSave: Array<Client> = [];
  appointID = "";

  userPhotoURL = "";
  auth: Auth | null = null;
  userInfo = "Thanh Tran";
  message = "";

  mounted(): void {
    this.auth = getAuth();
    onAuthStateChanged(this.auth, (user: User | null) => {
      console.log("Auth changed", user);
    });
    this.readData();
    this.SavePrice();
  }

  readData(): void {
    this.auth = getAuth();
    //this.auth_backup = this.auth;
    //var uid = this.auth.currentUser!.uid;
    this.auth.onAuthStateChanged((user) => {
      if (user != null) {
        const uid = user.uid;

        var myapp: FirebaseApp = initializeApp(firebaseConfig);
        var db: Firestore = getFirestore(myapp);

        const myAppoint = collection(db, uid);
        getDocs(myAppoint).then((qs: QuerySnapshot) => {
          qs.forEach((qd: QueryDocumentSnapshot) => {
            console.log("From Firestore: ", qd.id, qd.data());
            const details = qd.data();
            this.selectedClient.push({
              name: details.name,
              phoneNumber: details.phoneNumber,
              date: details.date,
              time: details.time,
              selectedServices: details.service,
              id: details.id,
            });
          });
        });
      }
    });
  }

  SaveData(): void {
    //this.readData();
    const idCheck = this.selectedClient
      .map((object) => object.id)
      .indexOf(this.clientID);
    if (idCheck == -1) {
      this.auth = getAuth();
      var uid = this.auth.currentUser!.uid;

      this.selectedClient.push({
        name: this.clientName,
        phoneNumber: this.clientNumber,
        date: this.selectedDate,
        time: this.selectedTime,
        selectedServices: this.selectedServices,
        id: this.clientID,
      });

      var myapp: FirebaseApp = initializeApp(firebaseConfig);
      var db: Firestore = getFirestore(myapp);

      // add a new document with our own uid
      this.selectedClient.forEach(
        async (z: {
          name: string;
          phoneNumber: string;
          date: any;
          time: any;
          selectedServices: string;
          id: string;
        }) => {
          var d = doc(db, uid, z.id);
          await setDoc(d, {
            name: z.name,
            phoneNum: z.phoneNumber,
            date: z.date,
            time: z.time,
            service: z.selectedServices,
            id: z.id,
          });
        }
      );
    } else {
      this.showMessage("Choose different ID for appointment");
    }
  }

  // Save data for price, all user have this data
  SavePrice(): void {
    //this.readData();
    var myapp: FirebaseApp = initializeApp(firebaseConfig);
    var db: Firestore = getFirestore(myapp);

    // add a new document with our own uid

    var d = doc(db, "perdicures", "service");
    setDoc(d, {
      name1: "pedicure",
      name2: "shellac pedicure",
      name3: "polish change",
    });
    var e = doc(db, "perdicures", "price");
    setDoc(e, { price1: "28", price2: "34", price3: "12" });
  }
  // read price data

  // delete user data
  DeleteAppoint(): void {
    this.auth = getAuth();
    var uid = this.auth.currentUser!.uid;

    var myapp: FirebaseApp = initializeApp(firebaseConfig);
    var db: Firestore = getFirestore(myapp);

    const illi = doc(db, uid, this.appointID);
    deleteDoc(illi).then(() => {
      console.log("appointment deleted");
      this.selectedClient.splice(0, this.selectedClient.length);
      this.readData();
    });
  }

  public chartOptions = {
    chart: {
      width: 380,
      type: "pie",
    },

    labels: ["Pedicure", "Shellac Pedicure", "Polish Change"],
    responsive: [
      {
        breakpoint: 480,
        options: {
          chart: {
            width: 700,
          },
          legend: {
            position: "right",
          },
        },
      },
    ],
  };
  public series = [28, 34, 12];

  showMessage(txt: string) {
    this.message = txt;
    // The message will automatically disappear after 5 seconds
    setTimeout(() => {
      this.message = "";
    }, 5000);
  }

  DeleteAcc(): void {
    this.auth = getAuth();
    var user = this.auth.currentUser;

    deleteUser(user!)
      .then(() => {
        this.showMessage(`Delete Account. Logging out`);
        this.outtahere();
      })
      .catch((error) => {
        this.showMessage(`Unable to delete account`);
      });
  }

  outtahere(): void {
    if (this.auth) signOut(this.auth);

    // Back to the previous page
    // this.$router.push('login');
    this.$router.push({ path: "/" });
  }
}
